.c4l-key-concept {

  &.is-style-compact-width .c4l-key-concept-content {
    width: 75%;
    margin-left: auto;
    margin-right: auto;
  }

  &.is-style-full-width .c4l-key-concept-content {
    width: 100%;
  }
}

.c4l-key-concept-content {
  box-sizing: border-box;
  width: 75%;
  background-color: #f1f5fe;
  padding: 24px 36px;
  border: none;
  border-left: 6px solid #387af1;
  margin: 36px auto;
  position: relative;
  border-radius: 0;

  p:last-of-type {
    margin-bottom: 0;
  }
}